import React from 'react';
import VideoTree from '../../Components/VideoTree/VideoTree.js';
import { generateRandomInt } from '../../utils/utils.js';
import BreadCrumbs from '../../Components/BreadCrumbs';
import Ajax from '../../utils/ajax';
import { isDev } from '../../utils/env';

class Edit extends React.Component {
  
    constructor(props) {
        super(props);

        this.state = {
            project: {
                title: '',
            },
            nodes: [], // todo nodes will be child of project
        };

        this.createNode = this.createNode.bind(this);
        this.updateNode = this.updateNode.bind(this);
        this.editProjectTitle = this.editProjectTitle.bind(this);
    }

    createNode(parentId) {
        let nodes = this.state.nodes.map(n => JSON.parse(JSON.stringify(n)));
        nodes.push({
            id: generateRandomInt(10000000, 90000000),
            parent_id: parentId,
            title: 'New Node',
            url: '',
        });

        this.setState({nodes});
    }

    updateNode(node) {
        let nodes = [...this.state.nodes];

        for (let i=0; i < nodes.length; i++) {
            if (node.id === nodes[i].id) {
                nodes[i] = node;
                this.setState({nodes});
                // TODO save node to server
                return;
            }
        }
    }

    editProjectTitle(newTitle) {
        let project = {...this.state.project};
        project.title = newTitle;

        console.log(this.props.match.url);
        if (!isDev()) {
            Ajax.put(this.props.match.url, project)
                .catch(err => console.error(err));
        }
        
        this.setState({project});
        // TODO save new title to server
    }

    componentDidMount() {
        console.log(process.env, process.env.PUBLIC_URL);

        // let project = {...this.state.project};
        // project.title = 'Test title';
        // this.setState({project, nodes: projectNodes});
        let url = `/projects/${this.props.match.params.id}`;
        if (process.env.NODE_ENV === 'development') {
            url = `${process.env.PUBLIC_URL}/data/project_1.json`;
        }
        Ajax.get(url, [])
            .then(res => {
                console.log(res);
                if (typeof res.project.id !== 'undefined') {
                    let state = {...this.state};
                    state.project = res.project;
                    if (typeof res.project.nodes !== 'undefined') {
                        state.nodes = res.project.nodes;
                    }
                    this.setState(state);    
                }
            })
            .catch(err => console.error(err));
    }

    render() {
        const links = [
            { name: 'projects', path: '/projects'},
            { name: this.state.project.title, path: `/projects/${this.props.match.params.id}`, edit: this.editProjectTitle},
        ];
        return (
            <>
                <BreadCrumbs links={links}/>
                <div style={{clear: 'both', marginBottom: '30px'}}></div>
                { 
                    this.state.nodes.length < 1 
                        ? ''
                        :   <VideoTree 
                                nodes={this.state.nodes} 
                                nodesKey='nodes' 
                                createNode={this.createNode}
                                updateNode={this.updateNode}
                            />
                }
            </>
        )
    }
}

export default Edit;
