import React from 'react';
import './NodeExpansion.css';


class NodeExpansion extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            node: {
                id: '',
                title: '',
                url: '',
                parent_id: false,
            },
        };

        this.handleChange = this.handleChange.bind(this);
        this.updateNode = this.updateNode.bind(this);
        this.changeParent = this.changeParent.bind(this);
    }

    handleChange(e) {
        // console.log(e.target.name);
        let node = {...this.state.node};
        node[e.target.name] = e.target.value;
        this.setState({node});
    }

    changeParent(id) {
        let node = {...this.state.node};
        node.parent_id = id;
        this.setState({node});
    }

    updateNode() {
        this.props.updateNode(this.state.node);
    }

    componentDidMount() {
        this.setState({node: this.props.node});
    }

    render() {
        const { node } = this.state;
        return (
            <span className="node-expansion">
                <input 
                    onChange={this.handleChange} 
                    name="title" 
                    value={node.title}
                />
                <input 
                    onChange={this.handleChange} 
                    name="url" 
                    value={node.url}
                />
                {
                    // todo exclude current parent_id and child node ids
                    this.props.availableParents.map(p => (
                        <span 
                            key={p.id} 
                            onClick={() => this.changeParent(p.id)}
                            style={{padding: '5px'}}>
                            {p.title}
                        </span>
                    ))
                }
                <button onClick={this.updateNode}>done</button>
            </span>
        );
    }

}

export default NodeExpansion;